body {
  letter-spacing: .02em;
  background-color: var(--white);
  color: var(--body-text);
  font-family: Roboto, sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.button-container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.load-more {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: .8em;
  font-weight: bold;
  transition: background-color .3s ease-in-out, transform .2s ease-in-out;
  display: inline-block;
}

.load-more-hidden {
  display: none;
}

.load-more:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

.load-more:disabled {
  cursor: not-allowed;
  background-color: #c1c1c1;
}

.search-form {
  justify-content: center;
  margin-bottom: 16px;
  display: flex;
}

.div-input {
  width: 500px;
  background: #f2f2f2;
  border: 1px solid #f2f2f2;
  padding: 5px;
}

.search-form input {
  height: 30px;
  width: calc(100% - 10px);
  background: none;
  border: none;
  outline: rgba(0, 0, 0, 0);
  padding-left: 10px;
  font-family: Fira Mono, monospace;
  font-size: 16px;
}

.search-form input:focus {
  border: 2px solid #007bff;
}

.search-form button {
  letter-spacing: .04em;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  font-weight: bold;
  transition: background-color .3s ease-in-out, transform .2s ease-in-out;
  display: inline-block;
}

.search-form button:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px;
  margin-bottom: 16px;
  display: flex;
}

.photo-card {
  max-width: 300px;
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 0 0 4px 4px;
  flex-direction: column;
  padding: 10px;
  display: flex;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.card-img {
  height: 200px;
  width: 100%;
}

.info {
  justify-content: center;
  align-items: center;
  gap: 4px;
  margin-top: 10px;
  display: flex;
}

.info-item {
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  font-size: 13px;
}

